@media screen and (min-width: 960px) {
    #root {
        margin: 10rem;
    }
}

.chat {
    max-height: 70vh;
    border: 1px solid #d8d8fd;
    border-radius: 4px;
    box-shadow: 0 0 9px 1px rgba(0, 0, 0, .1);
    display: flex;
    flex-direction: column;
}
.message-list {
    flex: 10 1;
    overflow-y: scroll;
}
.messages {
    display: flex;
    flex-direction: column-reverse;
    min-height: 100%;
    padding: 0 10px;
}
.input-message {
    width: 100%;
    margin: 5px;
    border-radius: 20px;
    border: 1px solid #eee;
    font-size: 16px;
    outline: none;
    padding: 4px 10px;
}
